.app-wrapper{
    display: grid;
    grid-template-areas: 'h h' 's c';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
    /*row-gap: 20px;*/
    font-family: sans-serif;
}

.header {
    grid-area: h;
}

.sidebar {
    grid-area: s;
}

.app-content {
    grid-area: c;
}

